import { VStack, Text, Wrap, UnorderedList, ListItem } from "@chakra-ui/react";
import { getYdAndM } from "@tanyoknits/shared";
import { introSizing } from "../projects/tiara-vest/sizes";
import { getNeedleText } from "../utils/needles";
import IntroCard from "./IntroCard";
import IntroSizingTable from "./IntroSizingTable";
import Photos from "./Photos";
import { ProjectProfile } from "../utils/types";

interface Props extends ProjectProfile {
  projectID: string;
}
export default function IntroComponent({
  projectID,
  yarn,
  needles,
  gauge,
  notions,
  difficulty,
  photoCount,
}: Props) {
  return (
    <VStack px={4} spacing={4} align="left">
      <Wrap spacing={4}>
        {photoCount > 0 && <Photos id={projectID} count={photoCount} />}
        <Wrap width={{ md: "100%", lg: "5xl" }} spacing={4}>
          <IntroCard title="🧶 Yarn">
            <>
              <a href={yarn.url} target="_parent">
                <Text variant="link">{yarn.line}</Text>
              </a>
              by {yarn.maker}
              <Text>Sample color: {yarn.color}</Text>
              <Text>
                1 {yarn.shape} = {getYdAndM(yarn.yards)} / {yarn.grams}g
              </Text>
            </>
          </IntroCard>
          <IntroCard title="🪡 Needles">
            <UnorderedList>
              {needles.map((needle, i) => (
                <ListItem key={i}>
                  {getNeedleText(needle.size)} {needle.type} - {needle.usage}
                </ListItem>
              ))}
            </UnorderedList>
          </IntroCard>
          <IntroCard title="📐 Gauge">
            <Text>
              {gauge.sts_count} sts and {gauge.row_count} rows = 4" / 10cm
            </Text>
            <Text>
              in {gauge.sts} with {getNeedleText(gauge.needle)}
            </Text>
          </IntroCard>
          <IntroCard title="🧷 Notions">
            <UnorderedList>
              {notions.map((notion, i) => (
                <ListItem key={i}>{notion}</ListItem>
              ))}
            </UnorderedList>
          </IntroCard>
          <IntroCard title="🌟 Difficulty">
            <Text>{difficulty}</Text>
          </IntroCard>
        </Wrap>
      </Wrap>
      <IntroCard title="🔢 Sizing & Measurement">
        <IntroSizingTable introSizing={introSizing} yarn={yarn} />
      </IntroCard>
    </VStack>
  );
}
