import { Box, Card } from "@chakra-ui/react";

interface Props {
  id: string;
  count: number;
}

function Photo({ id, number }: { id: string; number: string }) {
  return (
    <Card
      variant="elevated"
      height="xs"
      width="xs"
      style={{
        background: `url(${process.env.PUBLIC_URL}/photos/${id}-0${number}.jpg)`,
        backgroundSize: "cover",
      }}
    />
  );
}

export default function Photos({ id, count }: Props) {
  return (
    <Box width="xs">
      <Photo id={id} number="1" />
    </Box>
  );
}
